html {
  --blue-color: #00acdc;
  --nav-link-height: 60px;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.logo {
  margin-left: 50px;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  margin-right: 50px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin: 0 10px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  height: var(--nav-link-height);
  line-height: var(--nav-link-height);
  text-align: center;
  text-transform: uppercase;
  color: #000;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__link:hover, .nav__link[data-qa="hover"]:hover {
  color: var(--blue-color);
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
